@import "../theme.scss";

//Operation
.operation {
  display: flex;
  height: 45px;
  background: white;
  border-radius: 3px;
  border: 1px solid #ebebeb;
  padding: 0 15px;
  align-items: center;

  .checkAll {
    display: flex;
    align-items: center;

    i {
      margin-right: 5px;
      cursor: pointer;
      font-size: 20px;
      border-radius: 4px;
    }

    .checkAll-text {
      margin-right: 10px;
    }

    .invert-Selection {
      cursor: pointer;
      color: #FF6627;
    }
  }

  .btn {
    position: relative;
    margin-left: 48px;

    &::before {
      position: absolute;
      display: block;
      content: '';
      width: 1px;
      height: 26px;
      background-color: #8d8d8d;
      top: 2px;
      left: -24px;
    }
  }

