//PlaylistCell
.PlaylistCell {
  float: left;
  flex: 1;
  padding: 5px;

  .list_cell {
    height: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    border: 2px solid #fff;
    box-shadow: 0px 2px 6px 0px rgba(210, 222, 241, 1);
    background: rgba(255, 255, 255, 1);
    position: relative;

    .top_img {
      height: 62%;

      .album_top_img {
        height: 100%;
        position: relative;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        overflow: hidden;
      }

      .top_topImg {
        position: relative;
        overflow: hidden;
        height: 62.5%;
        margin-bottom: .5%;
        background-color: #f3f5f7;
        background-repeat: no-repeat;
        background-position: center;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
      }

      .top_bottomImgs {
        height: 37%;

        .img1,
        .img2,
        .img3 {
          float: left;
          width: 33%;
          height: 100%;
          background-color: #f3f5f7;
          background-repeat: no-repeat;
          background-position: center;
          position: relative;
          overflow: hidden;
        }

        .img1,
        .img2 {
          margin-right: .5%;
        }
      }
    }

    .information {
      flex: 1;
      padding: 7px 5px 0 5px;
      position: relative;

      .information_top {
        border-bottom: 1px solid #e7e7e7;
        padding-bottom: 7px;

        .name {
          width: 140px;
          font-size: 14px;
          color: rgba(0, 0, 0, 1);
          line-height: 19px;
          margin-bottom: 7px;
        }

        .time {
          height: 21px;

          span {
            display: block;
            float: left;
            background: rgba(243, 243, 243, 1);
            border-radius: 3px;
            font-size: 12px;
            font-weight: 400;
            color: rgba(144, 144, 144, 1);
            line-height: 19px;
            padding: 0 7px;
          }
        }
      }

      .information_bottom {
        margin-top: 3px;

        span {
          font-size: 12px;
          color: rgba(0, 0, 0, 0.65);
        }
      }

      .frames_def {
        position: absolute;
        top: 7px;
        right: 5px;
        display: block;
        width: 44px;
        height: 18px;
        background: $bg-color;
        border-radius: 3px;
        font-size: 12px;
        color: white;
        line-height: 18px;
        text-align: center;
      }
    }

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 100%;
      min-width: 100%;
      min-height: 100%;
      transform: translate(-50%, -50%);
    }

    &:hover {
      border: 2px solid $bg-color;
    }

    &:hover .icon-check-s {
      visibility: initial;
    }
  }

  .list_cell_check {
    border: 2px solid $bg-color;

    .icon-check-s {
      visibility: initial;
    }
  }
}